#landing {
    width: 100%;
    color: gainsboro;
    /* background-color: rgb(75, 75, 75); */

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-self: center;
}

#landing .title {
    font-size: 4rem;
    font-weight: 300;
    color: salmon;
    margin: 3rem 0 1rem 0;
}

.title2 {
    font-size: 3rem;
    font-weight: 300;
    color: gainsboro;
    margin: 3rem 0 4rem 0;
}

#landing > h2 {
    font-size: 1.5rem;
    font-weight: 300;
    margin: 0 0 3rem 0;
}

.landing1 {
    display: flex;
    flex-direction: column;
    align-self: center;
    width: 80%;
}

.hbox {
    background-color: black;
    box-sizing: border-box;
    padding: 1rem;
}

.hbox h3 {
    color: salmon;
    font-size: 1.2rem;
}

.hbox p {
    color: white;
    font-size: 1rem;
}


.imdiv1 {
    width: 100%;
    height: 600px;
    overflow: hidden;
}

.imdiv1 img {
    width: 100%;
    height: 600px;
    object-fit: cover;
    object-position: center;
}

.warning {
    background-color: salmon;
    color: black;
    margin: 4rem 0 2rem 0;
    padding: 2rem;
}

.warning > h1 {
    font-size: 3rem;
    font-weight: 300;
    margin: 1rem 0;
}



.landing2 {
    width: 80%;
    display: flex;
    flex-direction: row;
    align-self: center;
    margin-bottom: 3rem;
}

.imdiv2 {
    width: 100%;
    height: 600px;
    overflow: hidden;
}

.imdiv2 img {
    width: 100%;
    height: 600px;
    object-fit: cover;
    object-position: center;
}
.vbox {
    width: 100%;
    padding: 1rem;
    box-sizing: border-box;
    background-color: black;
    color: white;
    text-align: left;

}

.vbox h3 {
    color: salmon;
    font-size: 1.2rem;
}


.footer {
    width: 100%;
    background-color: black;
    color: white;
    text-align: center;
    padding: 1rem;
    box-sizing: border-box;
}

.footer a {
    color: gainsboro;
    text-decoration: none;
    margin: 10px;
}

.footer ul {
    list-style-type: none;
    display: flex;
    justify-content: center;
    padding: 1rem;
}


@media screen and (max-width: 900px) {

    #landing .title {
        font-size: 2.5rem;
    }

    .landing1 {
        width: 100%;
    }

    .landing2 {
        width: 100%;
        flex-direction: column-reverse;
    }


}